@include body-class(true) {

	&.sui-color-accessible {
		color: $accessible-dark;

		// Typography.
		h1, h2, h3, h4, h5, h6, p, p small, li, code, pre {
			color: $accessible-dark;
		}
		a {
			color: lighten($accessible-dark, 10%) !important;
			&:hover {
				color: $accessible-dark !important;
			}
		}

		pre,
		code {
			background: $accessible-light;
		}

		// Box Settings
		.sui-settings-label,
		.sui-description {
			color: $accessible-dark;
		}

		// Forms.
		.sui-form-control,
		.sui-label,
		.sui-error-message {
			border-color: $accessible-dark;
			color: $accessible-dark;
		}

		.sui-upload-button {
			color: $accessible-dark;
			background: $accessible-light;
		}

		.sui-upload-label {
			&:hover {
				.sui-upload-button {
					color: $accessible-light;
					background: $accessible-dark;
				}
			}
		}

		.select-container {
			.select-list-container {
				.list-value {
					border-color: $accessible-dark;
				}
				.list-results {
					li.sui-element-flex {
						span + span {
							color: $accessible-dark;
						}
					}
				}
			}

		}
		.sui-insert-variables {
			.sui-variables+.select2 {
				.select2-selection--single {
					border-color: $accessible-dark;
				}
			}

		}

		// Recipient.
		.sui-recipient {
			span {
				color: $accessible-dark;
			}
		}

		// Summary.
		.sui-summary {
			.sui-summary-segment {
				.sui-summary-details {
					.sui-summary-sub {
						color: $accessible-dark;
					}
				}

				.sui-list {
					.sui-list-label,
					.sui-list-detail {
						color: $accessible-dark;
					}
				}
			}

		}

		// Sidenav.
		.sui-row-with-sidenav {
			.sui-sidenav {
				.sui-vertical-tabs {
					a {
						color: $accessible-dark;
					}
				}
			}

		}

		// Tabs.
		.sui-tabs {
			[data-tabs] > * {
				color: $accessible-dark;
			}

		}
		
		// Toggle Tabs.
		.sui-side-tabs {
			.sui-tab-item {
				&.active {
					color: $accessible-light;
					background-color: $accessible-dark;
				}
			}
		}

		// Tags.
		.sui-tag {
			background: $accessible-dark;
			color: $accessible-light;
		}

		// Notifications.
		.sui-notice,
		.sui-notice-top {
			border-left-color: $accessible-dark;
			.sui-notice-dismiss a,
			p:first-of-type:before {
				color: $accessible-dark;
			}
		}

		// Icons.
		[class*="sui-icon-"] {
			&:before {
				color: $accessible-dark !important;
			}
		}

		// Progress bars.
		.sui-progress-state {
			.sui-progress-state-text {
				color: $accessible-dark;
			}
		}
		.sui-progress-block {
			.sui-progress {
				.sui-progress-bar {
					span {
						background: $accessible-dark;
					}
				}
			}

		}

		// Tables.
		.sui-table {
			border-color: $accessible-dark;
			th,
			td {
				border-color: $accessible-dark;
				color: $accessible-dark;
			}
		}

		// Accordions.
		.sui-accordion {
			.sui-accordion-open-indicator {
				i {
					color: $accessible-dark;
				}
			}
			.sui-accordion-item {
				&:hover {
					background: transparent;
				}
			}
			.sui-accordion-item-title {
				i {
					color: $accessible-dark;
				}
			}
		}

		// Scores.
		.sui-grade-a,
		.sui-grade-b,
		.sui-grade-c,
		.sui-grade-d,
		.sui-grade-e,
		.sui-grade-f  {
			svg circle:last-child {
				stroke: $accessible-dark;
			}
		}

		// Dropdowns.
		.sui-dropdown {
			ul {
				li {
					color: $accessible-dark;
					&:hover {
						color: $accessible-dark;
					}
				}
			}

		}

		// Buttons.
		.sui-button-icon:not(.sui-button),
		.sui-button {
			color: $accessible-light !important;
			background: $accessible-dark !important;
			i {
				&:before {
					color: $accessible-light !important;
				}
			}
			&.sui-button-ghost {
				padding: 7px 16px;
				border: 0;
			}

		}

		// Lists.
		.sui-list {
			.sui-list-label,
			.sui-list-detail {
				color: $accessible-dark;
			}
		}

		// Toggles.
		.sui-toggle {
			input[type=checkbox]:checked+.sui-toggle-slider {
				background-color: $accessible-dark;
			}
		}
		
		// Checkbox and Radio.
		.sui-checkbox,
		.sui-radio {
			input:checked+span {
				border-color: $accessible-dark;
				background-color: $accessible-dark;
			}
		}

		// Status dot.
		.sui-status-dot {
			span {
				background-color: $accessible-light;
				border: 1px solid $accessible-dark;
			}
			&.sui-published {
				span {
					background-color: $accessible-dark;
					border: none;
				}
			}
		}
		
		// WP Editor.
		.wp-editor-wrap {
			&.tmce-active {
				.wp-editor-tabs {
					.switch-tmce {
						color: $accessible-light;
						background-color: $accessible-dark;
					}
				}
			}
			&.html-active {
				.wp-editor-tabs {
					.switch-html {
						color: $accessible-light;
						background-color: $accessible-dark;
					}
				}
			}
			.wp-media-buttons {
				.insert-media {
					background-color: $accessible-dark !important;
				}
			}
		}
		
		// Ace editor selectors.
		.sui-ace-selectors {
			a.sui-selector {
				background-color: $accessible-dark;
				color: $accessible-light !important;
			}
		}
		
		// Pagination filters.
		.sui-pagination-wrap {
			.sui-pagination-filter {
				.sui-active-filter,
				.sui-active-filter-remove,
				.sui-active-filter-remove:hover {
					background-color: $accessible-dark;
				}
			}
		}
		
		// SUI box.
		.sui-box {
			.sui-box-title {
				color: $accessible-dark !important;
			}
		}
		
		// SUI selector box.
		.sui-box-selector {
			input:checked+span {
				background-color: $accessible-light !important;
				color: $accessible-dark;
				&:after {
					border-top: 40px solid $accessible-dark;
				}
			}
			input:checked+span [class*=sui-icon]:before {
				color: $accessible-dark;
			}
		}

		// Sidenav.
		.sui-row-with-sidenav {
			.sui-sidenav {
				.sui-vertical-tabs {
					.sui-vertical-tab.current {
						box-shadow: 0 0 0 1px $accessible-dark;
						background-color: transparent;
					}
				}
			}
		}

		// Select & Select2.
		.select-container {
			border-color: $accessible-dark;
			background-color: $accessible-light;
			.select-list-container {
				.list-results {
					border-color: $accessible-dark;
					li {
						color: $accessible-dark;
						&.current {
							background-color: $accessible-dark;
							color: $accessible-light;
						}
					}
				}
			}
		}

		select.sui-select + .select2-container {
			.select2-selection--single {
				border-color: $accessible-dark;
				background-color: $accessible-light;
				.select2-selection__arrow::before {
					color: $accessible-dark;
				}
				.select2-selection__rendered {
					color: $accessible-dark;
				}
			}

			.select2-selection--multiple {
				border-color: $accessible-dark;
				background-color: $accessible-light;
				.select2-selection__rendered {
					.select2-selection__choice {
						background-color: $accessible-dark;
					}
				}
			}
		}
	}
}

// Loaded outside of sui-wrap.
@include body-class() {
	// Select2.
	.sui-select-dropdown {
		border-color: $accessible-dark;
		&.sui-color-accessible {
			.select2-results {
				.select2-results__group,
				.select2-results__option {
					color: $accessible-dark;
				}
				> .select2-results__options {
					.select2-results__option {
						color: $accessible-dark;
						&[aria-selected=true] {
							background-color: $accessible-dark;
							color: $accessible-light;

						}
					}
					.select2-results__option--highlighted:not([aria-selected=true]) {
						color: $accessible-dark;
					}
				}
			}
			.select2-search--dropdown {
				.select2-search__field {
					border-color: $accessible-dark;
				}
			}
			.select2-results__message {
				color: $accessible-dark;
			}
			.select2-search--dropdown:after {
				color: $accessible-dark;
			}
		}
	}
}